import ADNDArmor from "./adndarmor.js";
import ADNDWeapon from "./adndweapon.js";

export function getAmmoTypes(weapon: ADNDWeapon): ADNDWeapon[] {
  let result: ADNDWeapon[] = [];

  if (weapon.name == "blowgun") {
    result = [
      new ADNDWeapon(
        "blowgun barbed dart",
        1 * 10,
        1,
        "small",
        "piercing",
        -1,
        "1d3",
        "1d2",
        "blowgun dart",
      ),
      new ADNDWeapon(
        "blowgun needle",
        1 * 10,
        1,
        "small",
        "piercing",
        -1,
        "1d3",
        "1d2",
        "blowgun dart",
      ),
    ];
  } else if (weapon.category == "bow") {
    result = [
      new ADNDWeapon(
        "flight arrow (12)",
        3 * 10,
        1,
        "small",
        "piercing",
        -1,
        "1d6",
        "1d6",
        "arrow",
      ),
      new ADNDWeapon("sheaf arrow (6)", 3 * 10, 1, "small", "piercing", -1, "1d8", "1d8", "arrow"),
    ];
  } else if (weapon.name == "Crossbow, hand") {
    result = [
      new ADNDWeapon(
        "hand quarrel",
        1 * 100,
        1,
        "small",
        "piercing",
        -1,
        "1d3",
        "1d2",
        "crossbow quarrel",
      ),
    ];
  } else if (weapon.name == "Crossbow, light") {
    result = [
      new ADNDWeapon(
        "light quarrel",
        1 * 10,
        1,
        "small",
        "piercing",
        -1,
        "1d4",
        "1d4",
        "crossbow quarrel",
      ),
    ];
  } else if (weapon.name == "crossbow, heavy") {
    result = [
      new ADNDWeapon(
        "heavy quarrel",
        2 * 10,
        1,
        "small",
        "piercing",
        -1,
        "1d4+1",
        "1d6+1",
        "crossbow quarrel",
      ),
    ];
  } else if (weapon.category == "sling") {
    result = [
      new ADNDWeapon(
        "sling bullet",
        1,
        0,
        "small",
        "bludgeoning",
        -1,
        "1d4+1",
        "1d6+1",
        "sling bullet",
      ),
      new ADNDWeapon("sling stone", 0, 0, "small", "bludgeoning", -1, "1d4", "1d4", "sling bullet"),
    ];
  }

  return result;
}

export function getArmor(): ADNDArmor[] {
  return [
    new ADNDArmor("banded mail", -7, 35, 300 * 100),
    new ADNDArmor("brigandine", -4, 35, 120 * 100),
    new ADNDArmor("bronze plate mail", -7, 45, 400 * 100),
    new ADNDArmor("chain mail", -6, 40, 75 * 100),
    new ADNDArmor("field plate", -8, 60, 2000 * 100),
    new ADNDArmor("full plate", -9, 70, 4000 * 100),
    new ADNDArmor("hide", -4, 30, 15 * 100),
    new ADNDArmor("leather", -2, 15, 5 * 100),
    new ADNDArmor("padded", -2, 10, 4 * 100),
    new ADNDArmor("plate mail", -7, 50, 600 * 100),
    new ADNDArmor("ring mail", -3, 30, 100 * 100),
    new ADNDArmor("scale mail", -4, 40, 120 * 100),
    new ADNDArmor("shield, body", -1, 15, 10 * 100),
    new ADNDArmor("shield, small buckler", -1, 10, 7 * 100),
    new ADNDArmor("shield, medium buckler", -1, 5, 3 * 100),
    new ADNDArmor("splint mail", -6, 40, 80 * 100),
    new ADNDArmor("studded leather", -4, 25, 20 * 100),
  ];
}

